.blog {
  margin-top: 113px;
  text-align: center;

  @include media-tablet {
    margin-top: 63px;
  }

  @include media-mobile {
    margin-top: 52px;
  }
}

.blog h2 {
  @include reset-text-slyle;
  @include section-titles;
  line-height: 44px;
  margin-bottom: 19px;
}

.blog a {
  @include reset-style-link;
}

.blog__articles {
  margin-top: 61px;

  @include media-desktop {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: min-content min-content;
    column-gap: 30px;
    row-gap: 30px;
  }

  @include tablet-mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 52px;
  }
}

.blog__article {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;

  @include tablet-mobile {
    margin: 0 auto 50px;
    width: 100%;
    align-items: stretch;
  }

  @include media-mobile {
    flex-direction: column;
    align-items: stretch;
    margin-bottom: 40px;
    width: 100%;
    max-width: 670px;
  }
}

.blog__article:nth-last-of-type(even) .blog__article-content {
  order: -1;

  @include media-mobile {
    order: 0;
  }
}

.blog__article a {
  position: relative;
  color: $dark-grey-text;

  &::after {
    position: absolute;
    content: "";
    height: 1px;
    width: 100%;
    background-color: $border-golden-color;
    left: 0;
    bottom: -5px;
  }
}

.blog__article a:hover {
  color: $golden-text;
}

.blog__article a:active {
  color: $light-grey-text;
  transform: scale(0.9);

  &::after {
    display: none;
  }
}

.blog__article img {
  width: 285px;
  height: 100%;
  max-height: 300px;
  object-fit: cover;

  @include media-tablet {
    width: 339px;
    height: 100%;
    max-width: unset;
  }

  @include media-mobile {
    width: 100%;
    max-height: 256px;
    max-width: 670px;
  }
}

@include media-desktop {
  .blog__article:last-of-type {
    position: relative;
    grid-row: 1/3;
    grid-column: 2/3;
  }
}

@include media-desktop {
  .blog__article:last-of-type img {
    width: 50vw;
    height: 100%;
    max-height: 100%;
  }
}

.blog__article:last-of-type .blog__article-content {
  position: absolute;
  right: 0;
  bottom: 0;

  @include media-tablet {
    padding: 85px 84px 56px;
  }

  @include tablet-mobile {
    position: static;
    padding-top: 62px;
    padding-bottom: 69px;
  }
}

.blog__article-content {
  width: 285px;
  min-height: 300px;
  padding: 68px 60px;
  background-color: $primary-beige-bg;

  @include media-tablet {
    padding: 68px 84px 56px;
    flex-grow: 1;
    width: auto;
  }

  @include media-mobile {
    padding: 45px 60px 53px;
    min-height: unset;
    width: 100%;
    max-width: 670px;
  }
}

.blog__article-content p,
.blog__article-content h3 {
  @include reset-text-slyle;
}

.blog__article-content h3 {
  font-family: $f-accent-semi-bold;
  font-size: 26px;
  line-height: 34px;
  margin-bottom: 35px;
}

.blog__article-content p {
  @include small-plain-text;
  line-height: 18px;
  color: $light-grey-text;
  margin-bottom: 12px;
}
